import { Component, Prop, State, Watch, h } from '@stencil/core';
import { DialogMetadata } from '../../interfaces/dialog-interfaces';

@Component({
  tag: 'description-component',
  styleUrl: 'description-component.css',
  shadow: false,
})
export class DescriptionComponent {
  @Prop() data: DialogMetadata;
  @Prop() sessionName: string;
  @State() showContent = false;

  title: string;

  @Watch('data')
  setData(newValue: DialogMetadata) {
    this.showContent = false;
    if (newValue) {
      if (newValue.title) {
        this.title = newValue.title;
      } else {
        this.title = newValue.label;
      }
    }

    this.data = newValue;
    setTimeout(() => {
      this.showContent = true;
    }, 100);
    console.log("desc,setData")
  }

  render() {
    return (
      <div class={`fade-in top ${this.showContent ? 'show' : ''}`}>
        {this.data?.metadata.description && (

          <div
            class="description-container"
            innerHTML={this.data?.metadata.description}>
          </div>

        )}
      </div>
    );
  }

}
